body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }

  .fullGame{
      margin-left: auto;
      margin-right: auto;
      width: 500px;
      height: 100vh;

  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .game {
      margin-left: 10%;
      background-color: rgba(143, 146, 155, 0.534);
      padding: 20px 0 40px 60px;
  }

  .square {
    border: 2px solid rgb(10, 5, 5);
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    padding: 0;
    text-align: center;
    width: 34px;
    color:rgb(3, 141, 14);
  }

  h1, h2 {
      text-align: center;
  }

  .resetButton {
      background-color: rgba(196, 2, 2, 0.671);
      color: white;
      text-align: center;
      text-transform: uppercase;
      border-radius: 10px;
      padding: 5px;
      position: absolute;
      margin-top: 110px;
      margin-left: -100px;
  }

  .historyButtons {
      background-color: rgb(5, 49, 182);
      color: white;
      border-radius: 10px;
      margin-top: 5px;  
      padding-left: 10px;
      padding-right: 10px;
      text-align: center;

  }

  .status {
      font-size: 2em;
      color:rgba(35, 47, 224, 0.733);
  }

  .movesOl {
      font-size: 1.2em;
      font-weight: bold;
      text-align: center;
  }

  li {
      list-style: none;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  